import React, { memo, useCallback, useMemo, useRef, useState, ReactElement} from 'react';
import { SliderContainer } from './styled';
import { IRestProps, mergeRestProps, useDefaultProp, renderReactNode } from '../../common';
import { SliderLine } from './SliderLine';
import { SliderPoint } from './SliderPoint';
import { SliderMarks } from './SliderMarks';

type SliderLegalValue = number | [number, number];
export interface SliderBaseProps<T extends SliderLegalValue>  {
  /**
   * 是否禁用
   */
  disable?: boolean;
  /**
   * 是否只能拖拽到刻度上
   */
  attachToMarksOnly?: boolean;
  /**
   * 标记刻度（吸附效果），并且必须在 min 和 max 之间
   */
  marks?: number[];

  /**
   * 最大值，默认 100
   */
  max?: number;

  /**
   * 最小值，默认 0
   */
  min?: number;

  /**
   * 步长，取值大于 0，且必须能被 (max - min) 整除
   */
  step?: number;

  /**
   * 双滑块模式	默认为false
   */
  range?: boolean;

  /**
   * 格式化文本
   */
  formatter?: (value: React.ReactText) => string;

  /**
   * 自定义渲染滑块，可指定style和class或设置颜色
   */
  point?: React.ReactElement;

  /**
   * 自定义渲染标记点，可指定style和class或设置颜色
   */
  mark?: React.ReactElement;

  /**
   * 自定义渲染滚动条，可指定style和class或设置颜色
   */
  line?: React.ReactElement;

  /**
   * 自定义tooltip，可指定style和class或设置颜色
   */
  tip?: React.ReactElement;

  /**
   * 受控 value 
   */
  value?: T;

  /**
   * 初始值 
   */
  defaultValue?: T;

  /**
   * 监听滑动条 value 的改变
   */
  onChange?:(value:T) => void;
}

export const Slider = memo(<T extends SliderLegalValue> (props: IRestProps & SliderBaseProps<T>) => {
  const {
    disable = false,
    attachToMarksOnly = false,
    range = false,
    marks = [],
    max = 100,
    min = 0,
    step = 1,
    value,
    defaultValue,
    onChange,
    formatter = (value: React.ReactText) => (`${value}`),
    point,
    mark,
    line,
    tip,
    ...rest
  } = props;
  const defaultStart = useMemo(() => {
    if (!range) return min;
    return (defaultValue as Array<number>)?.[0] ?? min;
  }, [range, defaultValue, min]);
  const defaultEnd = useMemo(() => {
    if (range) return (defaultValue as Array<number>)?.[1] ?? min;
    return (defaultValue as number) ?? min;
  }, [range, defaultValue, min]);
  const propsStart = useMemo(() => {
    if (!range) return min;
    return (value as Array<number>)?.[0];
  }, [value, range]);
  const propsEnd = useMemo(() => {
    if (range) return (value as Array<number>)?.[1];
    return value as number;
  }, [value, range]);

  const [start, setStart] = useDefaultProp<number>(defaultStart, propsStart, () => { });
  const [end, setEnd] = useDefaultProp<number>(defaultEnd, propsEnd, () => { });

  const sliderRef = useRef<HTMLDivElement>(null);
  const moveStart = useRef(false);
  const [isMove, setIsMove] = useState(false);
  const sliderLength = useMemo(() => max - min, [max, min]);
  // 计算移动距离
  const calculateDistance = useCallback((e: MouseEvent): number => {
    if (!sliderRef.current) return min;
    const { clientX: x } = e;
    const rect = sliderRef.current?.getBoundingClientRect();
    const { left: sliderLeft, right: sliderRight } = rect || {};
    let result = 0;
    if (sliderLeft && sliderRight) {
      if (x <= sliderLeft) {
        result = min;
      } else if (x >= sliderRight) {
        result = max;
      } else {
        let tmpValue = 0;
        let dist = Number.MAX_SAFE_INTEGER;
        if (!attachToMarksOnly) {
          const spanWidth = (sliderRight - sliderLeft) / ((max - min) / step);
          const order = Math.round((x - sliderLeft) / spanWidth);
          dist = Math.abs(spanWidth * order + sliderLeft - x);
          tmpValue = order * step;
        }
        marks.forEach((mark) => {
          const newDist = Math.abs(
            mark / (max - min) * (sliderRight - sliderLeft) + sliderLeft - x,
          );
          if (newDist < dist) {
            dist = newDist;
            tmpValue = mark;
          }
        });
        result = tmpValue;
      }
    }
    if (!Number.isInteger(result)) {
      result = Number(result.toFixed(6));
    }
    return result;
  }, [attachToMarksOnly, marks, max, min, step]);

  const handleChange = useCallback((point: number) => {
    const [sValue = 0, eValue = 0] = moveStart.current ? [point, end] : [start, point];
    const rangeValue: [number, number] =
      sValue > eValue ? [eValue, sValue] : [sValue, eValue];
    onChange?.((range ? rangeValue : point) as T);
  }, [onChange, range, start, end]);

  const handleMouseMove = useCallback((e: MouseEvent) => {
    const result = calculateDistance(e);
    if (moveStart.current) {
      setStart(result);
    } else {
      setEnd(result);
    }
    handleChange(result);
  }, [calculateDistance, handleChange]);

  const handleClickOnMark = useCallback((mark: number) => {
    if (disable) return;
    const isNearToStart = Math.abs(mark - (start ?? min)) < Math.abs((end ?? max) - mark);
    if (range && isNearToStart) {
      moveStart.current = true;
      setStart(mark);
    } else {
      moveStart.current = false;
      setEnd(mark);
    }
    handleChange(mark);
  }, [disable, handleChange, start, end, range, min, max]);

  const renderSliderMarks = useMemo(() => {
    const marksProps = { marks, handleClickOnMark, sliderLength, disable };
    return renderReactNode(mark, <SliderMarks {...marksProps} />, undefined);
  }, [marks, handleClickOnMark, sliderLength, mark, disable]);

  const handleMouseDownOnPoint = useCallback(() => {
    if (!disable) {
      setIsMove(true);
      document.addEventListener('mouseup', () => {
        document.removeEventListener('mousemove', handleMouseMove);
        setIsMove(false);
      });
      document.addEventListener('mousemove', handleMouseMove);
    }
  }, [disable, handleMouseMove]);

  const renderStartPoint = useMemo(() => {
    if (!range) return null;
    const pointProps = { range, handleMouseDownOnPoint, point: start, sliderLength, isMove, formatter, moveStart, isStart: true, disable, tip };
    return renderReactNode(point, <SliderPoint {...pointProps} />, undefined);
  }, [range, handleMouseDownOnPoint, start, sliderLength, isMove, formatter, point, disable, tip]);

  const renderEndPoint = useMemo(() => {
    const pointProps = { handleMouseDownOnPoint, point: end, sliderLength, isMove, formatter, moveStart, isStart: false, disable, tip };
    return renderReactNode(point, <SliderPoint {...pointProps} />, undefined);
  }, [handleMouseDownOnPoint, end, sliderLength, isMove, formatter, point, disable, tip]);

  const renderSliderLine = useMemo(() => {
    const lineProps = { start, end, sliderLength, disable };
    return renderReactNode(line, <SliderLine {...lineProps} />, undefined);
  }, [start, end, sliderLength, disable, line]);
  return (
    <SliderContainer
      ref={sliderRef}
      isMove={isMove}
      {...mergeRestProps(rest, 'wdn-slider')}
    >
      {renderSliderLine}
      {renderSliderMarks}
      {renderStartPoint}
      {renderEndPoint}
    </SliderContainer>
  );
}) as <T extends SliderLegalValue>(props: IRestProps & SliderBaseProps<T>) => ReactElement;
